/**
 * RISIVO CRM - Custom Login API Route
 * FIXED: Exclude avatar from JWT (it's a huge base64 image!)
 */

import { NextRequest, NextResponse } from 'next/server'

export const runtime = 'nodejs'
export const dynamic = 'force-dynamic'

// POST handler
export async function POST(req: NextRequest): Promise<NextResponse> {
  try {
    // Step 1: Parse body
    let body
    try {
      body = await req.json()
    } catch {
      return NextResponse.json({ error: 'Invalid JSON', step: 'parse_body' }, { status: 400 })
    }
    
    const { email, password } = body || {}
    if (!email || !password) {
      return NextResponse.json({ error: 'Missing credentials', step: 'validate' }, { status: 400 })
    }

    // Step 2: Import bcrypt
    let bcrypt
    try {
      bcrypt = (await import('bcryptjs')).default
    } catch (e: any) {
      return NextResponse.json({ error: 'bcrypt import failed', message: e.message, step: 'bcrypt_import' }, { status: 500 })
    }

    // Step 3: Import Prisma
    let PrismaClient
    try {
      PrismaClient = (await import('@prisma/client')).PrismaClient
    } catch (e: any) {
      return NextResponse.json({ error: 'prisma import failed', message: e.message, step: 'prisma_import' }, { status: 500 })
    }

    // Step 4: Create Prisma instance
    let prisma
    try {
      prisma = new PrismaClient()
    } catch (e: any) {
      return NextResponse.json({ error: 'prisma create failed', message: e.message, step: 'prisma_create' }, { status: 500 })
    }

    // Step 5: Query user - only select what we need!
    let user
    try {
      user = await prisma.user.findUnique({
        where: { email: email.toLowerCase().trim() },
        select: {
          id: true,
          email: true,
          name: true,
          password: true,
          role: true,
          status: true,
          isMasterAdmin: true,
          agencyId: true,
          avatar: true,  // Need this for response, but NOT for JWT
          agency: {
            select: {
              name: true
            }
          }
        }
      })
    } catch (e: any) {
      return NextResponse.json({ error: 'db query failed', message: e.message, step: 'db_query' }, { status: 500 })
    }

    if (!user || !user.password) {
      return NextResponse.json({ error: 'Invalid email or password', step: 'user_check' }, { status: 401 })
    }

    if (user.status !== 'active') {
      return NextResponse.json({ error: 'Account not active', step: 'status_check' }, { status: 403 })
    }

    // Step 6: Compare password
    let validPassword
    try {
      validPassword = await bcrypt.compare(password, user.password)
    } catch (e: any) {
      return NextResponse.json({ error: 'bcrypt compare failed', message: e.message, step: 'bcrypt_compare' }, { status: 500 })
    }

    if (!validPassword) {
      return NextResponse.json({ error: 'Invalid email or password', step: 'password_check' }, { status: 401 })
    }

    // Step 7: Get secret
    const secret = process.env.NEXTAUTH_SECRET || process.env.AUTH_SECRET
    if (!secret) {
      return NextResponse.json({ error: 'No secret', step: 'secret_check' }, { status: 500 })
    }

    // Step 8: Create JWT - DO NOT include avatar (it's a huge base64 string!)
    let token
    try {
      const crypto = require('crypto')
      const header = { alg: 'HS256', typ: 'JWT' }
      const now = Math.floor(Date.now() / 1000)
      const maxAge = 30 * 24 * 60 * 60
      const displayName = (user.name || '').replace('|', ' ').trim()
      const agencyName = user.agency?.name || 'Unknown'
      
      // IMPORTANT: Do NOT include avatar in JWT - it's a huge base64 image!
      const payload = {
        id: user.id,
        email: user.email,
        name: displayName,
        role: user.role,
        isMasterAdmin: user.isMasterAdmin || false,
        status: user.status || 'active',
        agencyId: user.agencyId || null,
        agencyName: agencyName,
        sub: user.id,
        iat: now,
        exp: now + maxAge,
      }
      
      const b64Header = Buffer.from(JSON.stringify(header)).toString('base64url')
      const b64Payload = Buffer.from(JSON.stringify(payload)).toString('base64url')
      const sig = crypto.createHmac('sha256', secret).update(`${b64Header}.${b64Payload}`).digest('base64url')
      token = `${b64Header}.${b64Payload}.${sig}`
    } catch (e: any) {
      return NextResponse.json({ error: 'jwt failed', message: e.message, step: 'jwt_create' }, { status: 500 })
    }

    // Step 9: Update stats (non-blocking)
    prisma.user.update({
      where: { id: user.id },
      data: { lastLoginAt: new Date(), lastActiveAt: new Date(), loginCount: { increment: 1 } }
    }).catch(() => {})

    // Step 10: Create response with cookie
    const isProduction = process.env.NODE_ENV === 'production'
    const cookieName = isProduction ? '__Secure-authjs.session-token' : 'authjs.session-token'
    const maxAge = 30 * 24 * 60 * 60
    const displayName = (user.name || '').replace('|', ' ').trim()

    const response = NextResponse.json({
      success: true,
      user: {
        id: user.id,
        email: user.email,
        name: displayName,
        role: user.role,
        isMasterAdmin: user.isMasterAdmin,
        agencyId: user.agencyId,
        agencyName: user.agency?.name,
        avatar: user.avatar,  // Include in response for UI, but NOT in JWT
      }
    })

    response.cookies.set({
      name: cookieName,
      value: token,
      expires: new Date(Date.now() + maxAge * 1000),
      httpOnly: true,
      sameSite: 'lax',
      secure: isProduction,
      path: '/',
    })

    return response

  } catch (error: any) {
    return NextResponse.json({
      error: 'Unexpected error',
      message: error?.message || 'Unknown',
      step: 'outer_catch'
    }, { status: 500 })
  }
}

// OPTIONS for CORS
export async function OPTIONS(): Promise<NextResponse> {
  return new NextResponse(null, {
    status: 204,
    headers: {
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
      'Access-Control-Allow-Headers': 'Content-Type',
    },
  })
}

// GET handler
export async function GET(): Promise<NextResponse> {
  return NextResponse.json({ status: 'ok', endpoint: '/api/login' })
}
